.footer {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	/* gap: 50px; */
	gap: 50px;
}

.footer-logo {
	display: flex;
	align-items: center;
	gap: 20px;
	gap: min(1.2svh, 1.2svw);
}

.footer-logo p {
	color: #383838;
	font-size: 46px;
	font-weight: 700;
}

.footer-links {
	display: flex;
	list-style: none;
	gap: 50px;
	color: #252525;
	font-size: 20px;
	padding-left: 0;
}

.footer-links li {
	cursor: pointer;
}

.footer-social-icon {
	display: flex;
	gap: 20px;
}

.footer-icons-container {
	padding: 10px;
	padding-bottom: 6px;
	background: #fbfbfb;
	border: 1px solid #e3e3e3;
}

.footer-copyright {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 30px;
	width: 100%;
	margin-bottom: 30px;
	color: #1a1a1a;
	font-size: 20px;
}

.footer-copyright hr {
	width: 85%;
	border: none;
	border-radius: 10px;
	height: 3px;
	background: #c7c7c7;
}

@media(max-width: 800px){
	.footer {
		gap: 20px;
	}

	.footer-logo {
		gap: 10px;
		/* align-items: center; */
	}

	.footer-logo img {
		width: 40px;
	}

	.footer-logo p {
		font-size: 25px;
	}

	.footer-links {
		/* align-self: flex-start; */
		font-size: 14px;
		gap: 10px;
	}

	.footer-social-icon img {
		width: 22px;
	}

	.footer-copyright {
		font-size: 13px;
	}
}

@media(max-width: 550px){
	.footer {
		gap: 15px;
	}

	.footer-logo {
		gap: 8px;
	}

	.footer-logo img {
		width: 36px;
	}

	.footer-logo p {
		font-size: 22px;
	}

	.footer-links {
		font-size: 11px;
		gap: 8px;
	}

	.footer-social-icon img {
		width: 20px;
	}

	.footer-copyright {
		font-size: 11px;
	}
}
