.cartitems {
	/* margin: 100px 170px; */
	display: flex;
	flex-direction: column;


	margin: 100px auto;
	text-align: left;
	width: fit-content;
	max-width: 1400px;
}

.cartitems hr {
	height: 3px;
	background: #e2e2e2;
	border: 0;
}

.cartitems-format-main {
	display: grid;
	grid-template-columns: .7fr 2fr .8fr 1fr .8fr .8fr;
	align-items: center;
	/* justify-content: end; */
	gap: 75px;
	padding: 20px 0px;
	color: #454545;
	font-size: 18px;
	font-weight: 600;

	/* background: red; */
}

.center {
	text-align: center;
	/* font-size: 90px; */
}

.cartitems-format {
	font-size: 17px;
	font-weight: 500;
}

.cartitems-product-icon {
	/* height: 62px; */
	width: 70px;
}

.cartitems-quantity-container {
	display: flex;
	align-items: center;
	flex-direction: row;
	justify-content: center;
	/* gap: 20px; */

	/* width: fit-content; */

	/* background-color: red; */
}

.cartitems-quantity {
	width: 50px;
	height: 45px;
	border: 2px solid #ebebeb;
	background: #fff;
	font-size: 16px;
}

.cartitems-quantity-change {
	width: 34px;
	height: 30px;
	border: none;
	background: #fff;
	font-size: 12px;
	cursor: pointer;
}

.cartitems-remove-icon {
	width: 15px;
	margin: auto;
	cursor: pointer;
}

.cartitems-down {
	display: flex;
	margin: 100px 0px;
}

.cartitems-total {
	flex: 1;
	display: flex;
	flex-direction: column;
	margin-right: 200px;
	gap: 40px;
}

.cartitems-total-item {
	display: flex;
	justify-content: space-between;
	padding: 15px 0px;
}

.cartitems-total button {
	width: 262px;
	height: 58px;
	outline: none;
	border: none;
	background: #ff5a5a;
	color: #fff;
	font-size: 16px;
	font-weight: 600;
	cursor: pointer;
}

.cartitems-promocode {
	flex: 1;
	font-size: 16px;
	font-weight: 500;
}

.cartitems-promocode p {
	color: #555;
}

.cartitems-promobox {
	width: 504px;
	margin-top: 15px;
	padding-left: 20px;
	height: 58px;
	background: #eaeaea;
}

.cartitems-promobox input {
	border: none;
	outline: none;
	background: transparent;
	font-size: 16px;
	width: 330px;
	height: 50px;
}

.cartitems-promobox button {
	width: 170px;
	height: 58px;
	font-size: 16px;
	background: black;
	color: #fff;
	cursor: pointer;
}

@media(max-width: 1280px) {
	.cartitems {
		margin: 60px 50px;
	}
	.cartitems-format-main {
		grid-template-columns: 0.5fr 3fr 0.5fr .5fr 0.5fr 0.5fr;
	}
}