.new-collections {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 10px;
	margin-bottom: 100px;
}

.new-collections h1 {
	color: #171717;
	font-size: 50px;
	font-weight: 600;

}

.new-collections hr {
	width: 200px;
	height: 6px;
	border-radius: 10px;
	background: #252525;
}

.collections {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	margin-top: 50px;
	gap: 30px;
}

@media(max-width: 1280px){
	.new-collections {
		/* height: 85vh; */
		margin-top: 65px;
	}

	.new-collections h1 {
		font-size: 40px;
	}

	.new-collections hr {
		width: 160px;
		height: 4px;
	}

	.collections {
		gap: 20px;
		margin-top: 30px;
	}

}

@media(max-width: 1024px){
	.new-collections {
		/* height: 80vh; */
		margin-top: 4%;
	}

	.new-collections h1 {
		margin-top: 5%;
		font-size: 30px;
	}

	.new-collections hr {
		width: 120px;
		height: 3px;
	}

	.collections {
		gap: 15px;
		margin-top: 20px;
	}

}

@media(max-width: 800px){
	.new-collections {
		/* height: 76vh; */
		margin-top: 11vh;
	}

	.new-collections h1 {
		font-size: 20px;
	}

	.new-collections hr {
		width: 100px;
		height: 2px;
	}

	.collections {
		gap: 5px;
		margin-top: 10px;
	}
}

@media(max-width: 500px){
	.new-collections {
		/* height: 90vh; */
		margin-top: 15%;
		/* justify-content: center; */
	}

	.collections {
		display: grid;
		grid-template-columns: 1fr 1fr;
		gap: 20px;
	}
}